//    faker    
import { faker } from '@faker-js/faker';

let idCategoryCount = 1;
let idProductCount = 1;
let idCostCount = 1;
let idProfitCount = 1;

export type Category = {
  id: string;
  name: string;
  photo?: string;
};

/**
 *  (Product) 
 * - id ()
 * - name ()
 * - photo ()
 * - desc (, )
 * - createdAt ()
 * - oldPrice (, )
 * - price ()
 * - category ()
 * */

export type Product = {
  id: string;
  name: string;
  photo: string;
  desc?: string;
  createdAt: string;
  oldPrice?: number;
  price: number;
  category: Category;
};

/*
 *  (Operation)     (Cost),   (Profit)
 * */

export type Operation = Cost | Profit;

/*
 *  (Cost) 
 * - id ()
 * - name ()
 * - desc (, )
 * - createdAt ()
 * - amount ()
 * - category ()
 * - type ('Cost')
 * */

export type Cost = {
  id: string;
  name: string;
  desc?: string;
  createdAt: string;
  amount: number;
  category: Category;
  type: 'Cost';
};

/*
 *  (Profit) 
 * - id ()
 * - name ()
 * - desc (, )
 * - createdAt ()
 * - amount ()
 * - category ()
 * - type ('Profit')
 * */

export type Profit = {
  id: string;
  name: string;
  desc?: string;
  createdAt: string;
  amount: number;
  category: Category;
  type: 'Profit';
};

/**
 *    (Product).
 *    ()
 * */

export const createRandomProduct = (
  createdAt: string,
  name: Product['name'] = faker.food.dish(),
  photo: Product['photo'] = faker.image.url(),
  desc: Product['desc'] = faker.food.description(),
  price: Product['price'] = faker.number.int({ min: 100, max: 1000 }),
  categoryName: Product['category']['name'] = faker.food.ethnicCategory(),
  oldPrice: Product['oldPrice'] = faker.number.int({ min: 100, max: 1000 })
): Product => {
  const product: Product = {
    id: `P-${idProductCount}`,
    name: name,
    photo: photo,
    desc: desc,
    createdAt: createdAt,
    oldPrice: oldPrice,
    price: price,
    category: { id: `C-${idCategoryCount}`, name: categoryName },
  };
  idCategoryCount += 1;
  idProductCount += 1;
  return product;
};

/**
 *    (Operation).
 *    ()
 * */
export const createRandomOperation = (createdAt: string): Operation => {
  const randomTypeOperation = Boolean(faker.number.int({ min: 0, max: 1 }));
  const operation: Operation = {
    id: randomTypeOperation ? `OC-${idCostCount}` : `OP-${idProfitCount}`,
    name: randomTypeOperation ? 'Purchase' : 'Sale',
    createdAt: createdAt,
    desc: faker.food.description(),
    amount: faker.number.int({ min: 1, max: 10 }),
    category: { id: `C-${idCategoryCount}`, name: faker.food.ethnicCategory() },
    type: randomTypeOperation ? 'Cost' : 'Profit',
  };
  if (randomTypeOperation) {
    idCostCount += 1;
  } else {
    idProfitCount += 1;
  }
  return operation;
};
